
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root{
  --color: royalblue;
}

html{
  color: white;
  font-family: 'Raleway', sans-serif;
}

body{
  width: 100%;
  height: 100vh;
  background-image: url(/weather/static/media/kielce.c53d949f.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-color: black;
  max-width: 1600px;
  text-align: center;
}

.mainContainer{
  width: 80%;
  margin: 0 auto;
}

.background{
  position: absolute;
  z-index: -1;
  /*background-color: rgba(255,255,255,0.1);*/
  background-color: rgba(0,0,0,0.7);
  width: 100%;
  height: 100vh;
}

span{
  color: royalblue;
  color: var(--color);
}


section.citiesSection{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
}

.cities{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 50%;

}

.cities p{
    padding: 0.5rem;
}
header{
    padding: 2rem;
}

header h1, header h3, header p {
    padding: 0.5rem;
}

header h3{
    color: royalblue;
}
.resultSection h1, .resultSection h3{
    padding: 0.5rem;
}

.resultSection span {
    font-size: 2rem;
    padding: 5px;
}

.result {
    font-size: 2.5rem;
}
.userChoice{
    width: 50%;
}

.userChoice h3, .userChoice form, .userChoice p, .userChoice h3{
    padding: 0.5rem;
}

.userChoice input{
    padding: 1rem;
    text-align: center;
}

.userChoice button{
    display: block;
    margin: 1rem auto 0 auto;
    font-size: 1rem;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0.5rem 1rem;
    background-color: royalblue;
    border: 5px solid royalblue;
    cursor: pointer;
    border-radius: 5%;
}

button:focus{outline: 0}

.userChoice button:hover{
    background-color: transparent;
    color: white;
    transition: 0.4s;

}
.error{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
}

.errorMessage{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    border: 10px solid white;
    padding: 20px;
    background-color: black;
}

.errorMessage h1{
    display: inline-block;
    padding: 0.5rem 10rem;
    margin-bottom: 2rem;
    border-bottom: 2px solid white;
}

.errorMessage button {
    font-size: 1.2rem;
    text-transform: uppercase;
    padding: 0.5rem 1rem;
    margin: 1rem 0;
    background-color: white;
    color: black;
    border: 2px solid white;
    cursor: pointer;
}

.errorMessage button:hover{
    background-color: black;
    color: white;
    transition: 0.4s;
}

